/* global jQuery */

/**
 * WordPress dependencies
 */
import ServerSideRenderer from '@wordpress/server-side-render';
import { __ } from '@wordpress/i18n';
import {
	Disabled,
	PanelBody,
	Placeholder,
	SelectControl,
	Spinner,
	ToggleControl,
} from '@wordpress/components';
import { use, useSelect } from '@wordpress/data';
import { useEffect, useState } from '@wordpress/element';
import { InspectorControls } from '@wordpress/block-editor';

/**
 * Internal dependencies
 */
import icon from './icon.js';

function Overlay( { children } ) {
	return (
		<div
			style={ {
				position: 'absolute',
				top: 0,
				right: 0,
				bottom: 0,
				left: 0,
				backgroundColor: 'rgba( 255, 255, 255, 0.8 )',
				display: 'flex',
				justifyContent: 'center',
				alignItems: 'center',
				zIndex: 99999,
			} }
		>
			{ children }
		</div>
	);
}

const EMPTY_ARRAY = [];

function Edit( { attributes, setAttributes } ) {
	const [ isFormInitialized, setFormInitialized ] = useState( false );
	const { formId, showTitle, showDescription } = attributes;

	const blockList = useSelect(
		( select ) => select( 'core/block-editor' ).getBlocks(),
		[]
	);

	useEffect( () => {
		if ( isFormInitialized ) {
			return;
		}

		blockList.forEach( ( block ) => {
			const {
				clientId,
				attributes: { formId: blockFormId },
				name,
			} = block;

			if ( 'simpay/payment-form' !== name ) {
				return;
			}

			if ( ! blockFormId || blockFormId !== formId ) {
				return;
			}

			return setTimeout( () => {
				const paymentFormInstance = jQuery(
					`#block-${ clientId } #simpay-block-payment-form-${ blockFormId }`
				);

				const formData = paymentFormInstance.data( 'form-vars' );
				console.log( formData );

				window.wpsp.initPaymentForm( paymentFormInstance, formData );

				setFormInitialized( true );
			}, 1500 );
		} );
	}, [ blockList, isFormInitialized ] );

	useEffect( () => {
		setFormInitialized( false );
	}, [ attributes ] );

	const { paymentForms, isLoading, hasPaymentForms } = useSelect(
		( select ) => {
			const { getEntityRecords, isResolving } = select( 'core' );
			const shape = [
				'postType',
				'simple-pay',
				{
					per_page: -1,
				},
			];

			const forms = getEntityRecords( ...shape );
			const loading = isResolving( 'getEntityRecords', shape );

			return {
				paymentForms: forms || EMPTY_ARRAY,
				isLoading: loading,
				hasPaymentForms: !! forms?.length,
			};
		},
		[]
	);

	const paymentFormOptions = [
		{
			label: __( 'Select a form', 'simple-pay' ),
			value: 0,
		},
		...paymentForms.map( ( { id, payment_form_title: label } ) => ( {
			label,
			value: id,
		} ) ),
	];

	const selectFormControl = (
		<SelectControl
			label={ __( 'Select a payment form', 'simple-pay' ) }
			value={ formId }
			onChange={ ( newFormId ) =>
				setAttributes( { formId: parseInt( newFormId ) } )
			}
			options={ paymentFormOptions }
		/>
	);

	if ( ! formId ) {
		return (
			<Placeholder
				icon={ icon }
				label={ __( 'WP Simple Pay - Payment Form', 'simple-pay' ) }
			>
				{ isLoading || ! hasPaymentForms ? (
					<Spinner />
				) : (
					selectFormControl
				) }
			</Placeholder>
		);
	}

	return (
		<Disabled>
			{ ! isFormInitialized && (
				<Overlay>
					<Spinner />
				</Overlay>
			) }
			<InspectorControls>
				<PanelBody title={ __( 'Form Settings', 'simple-pay' ) }>
					{ selectFormControl }

					<ToggleControl
						label={ __( 'Show Title', 'simple-pay' ) }
						checked={ showTitle }
						onChange={ () =>
							setAttributes( {
								showTitle: ! showTitle,
							} )
						}
					/>

					<ToggleControl
						label={ __( 'Show Description', 'simple-pay' ) }
						checked={ showDescription }
						onChange={ () =>
							setAttributes( {
								showDescription: ! showDescription,
							} )
						}
					/>
				</PanelBody>
			</InspectorControls>
			<ServerSideRenderer
				block="simpay/payment-form"
				attributes={ attributes }
			/>
		</Disabled>
	);
}

export default Edit;
